<template>
    <div style="width: 500px;margin-top: 10px">
        <el-input
                style="margin-bottom: 10px"
                size="small"
                prefix-icon="el-icon-search"
                placeholder="请输入部门名称进行搜索..."
                v-model="filterText">
        </el-input>

        <el-tree
                element-loading-text="正在加载..."
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(255, 255, 255, 0.8)"
                :expand-on-click-node="false"
                :data="deps"
                :props="defaultProps"
                :filter-node-method="filterNode"
                ref="tree">
            <span class="custom-tree-node" style="display: flex;justify-content: space-between;width: 100%"
                  slot-scope="{ node, data }">
        <span>{{ node.label }}</span>
        <span>
          <el-button
                  class="depBtn"
                  type="primary"
                  size="mini"
                  @click="() => showAddDepView(data)">
            添加部门
          </el-button>
          <el-button
                  class="depBtn"
                  type="danger"
                  size="mini"
                  @click="() => deleteDep(data)">
            删除部门
          </el-button>
        </span>
      </span>
        </el-tree>
        <el-dialog
                style="height: 360px"
                title="添加部门"
                :visible.sync="dialogVisible"
                width="30%"
        >
            <div>
                <div>
                    <el-tag size="small">上级部门</el-tag>
                    <span>{{pname}}</span>
                </div>
                <div>
                    <el-tag size="small">部门名称</el-tag>
                    <el-input size="mini" style="width: 250px" v-model="dep.name" placeholder="请输入部门名称..."></el-input>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false" size="small">取 消</el-button>
    <el-button type="primary" @click="doAddDep" size="small">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "DepMana",
        data() {
            return {
                filterText: '',
                deps: [],
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                dialogVisible: false,
                dep: {
                    name: '',
                    parentId: -1
                },
                pname: '',
                loading: false
            }
        },
        watch: {
            filterText(val) {
                this.$refs.tree.filter(val);
            }
        },
        mounted() {
            this.initDeps();
        },
        methods: {
            initDeps() {
                this.loading = true;
                this.getRequest("/system/basic/department/").then(resp => {
                    this.loading = false;
                    if (resp) {
                        this.deps = resp;
                    }
                })
            },
            filterNode(value, data) {//value为input输入的最新值，data为josn数组中的每一项
                if (!value) return true;
                return data.name.indexOf(value) !== -1;
            },
            showAddDepView(data) {
                this.pname = data.name;
                this.dep.parentId = data.id;
                this.dialogVisible = true;
            },
            removeDepFromDeps(deps, id) {
                for (let i = 0; i < deps.length; i++) {
                    let d = deps[i];
                    if (d.id == id) {
                        deps.splice(i, 1);
                        return;
                    } else {
                        this.removeDepFromDeps(d.children, id);
                    }
                }
            },
            deleteDep(data) {
                if (data.parent) {
                    this.$message.error("父部门删除失败")
                } else {
                    this.$confirm('此操作将永久删除【' + data.name + '】, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.deleteRequest("/system/basic/department/" + data.id).then(resp => {
                            if (resp) {
                                this.removeDepFromDeps(this.deps, data.id);
                            }
                        })
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });
                    });
                }
            },
            //直接重新加载deps的话，tree控件会折叠
            //动态的将服务端返回的数据添加到tree中
            addDep2Deps(deps, dep) {
                for (let i = 0; i < deps.length; i++) {
                    let d = deps[i];
                    //如果dep是deps的某一项的值
                    if (d.id == dep.parentId) {
                        //则将dep拼接到deps中
                        d.children = d.children.concat(dep);
                        return;
                    } else {
                        //递归调用 用deps的children来判断
                        this.addDep2Deps(d.children, dep);
                    }
                }
            },
            doAddDep() {
                this.postRequest("/system/basic/department/", this.dep).then(resp => {
                    if (resp) {
                        this.addDep2Deps(this.deps, resp.obj);
                        this.dep = {
                            name: '',
                            parentId: -1
                        };
                        this.pname = '';
                    }
                });
                this.dialogVisible = false;

            }
        },
    }
</script>

<style>
    .depBtn {
        padding: 2px;
    }

    .el-tag {
        margin-right: 10px;
    }
</style>