<template>
    <div>
        <div class="permissManaTool">
            <el-input size="small" placeholder="请输入角色英文名" v-model="role.name">
                <template slot="prepend">Role_</template>
            </el-input>
            <el-input size="small" placeholder="请输入角色中文名" v-model="role.nameZh" @keydown.enter.native="doAddRole">
            </el-input>
            <el-button icon="el-icon-plus" size="small" type="primary" @click="doAddRole">添加角色</el-button>
        </div>
        <div class="permissManaMain">
            <el-collapse v-model="activeName" accordion @change="change">
                <el-collapse-item
                        v-loading="loading"
                        element-loading-text="正在加载..."
                        element-loading-spinner="el-icon-loading"
                        element-loading-background="rgba(255, 255, 255, 0.8)"
                        :title="r.nameZh"
                        :name="r.id"
                        v-for="(r,index) in roles"
                        :key="index">
                    <el-card class="box-card">
                        <div slot="header" class="clearfix">
                            <span>可访问的资源</span>
                            <el-button style="float: right; padding: 3px 0;color: #ff3631;"
                                       icon="el-icon-delete-solid" type="text" @click="deleteRole(r)"></el-button>
                        </div>
                        <div>
                            <el-tree
                                    ref="tree"
                                    show-checkbox node-key="id"
                                    :key="index"
                                    :default-checked-keys="selectedMenus"
                                    :data="allmenus"
                                    :props="defaultProps"></el-tree>
                        </div>
                        <div style="display: flex;justify-content: flex-end">
                            <el-button size="mini" @click="cancelUpdate">取消修改</el-button>
                            <el-button size="mini" type="primary" @click="doUpdate(r.id,index)">确认修改</el-button>
                        </div>
                    </el-card>
                </el-collapse-item>
            </el-collapse>
        </div>
    </div>
</template>

<script>
    export default {
        name: "PermissMana",
        data() {
            return {
                role: {
                    name: '',
                    nameZh: ''
                },
                roles: [],
                allmenus: [],
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                selectedMenus: [],
                activeName: -1,
                loading: false
            }
        },
        mounted() {
            this.initRoles();
        }
        ,
        methods: {
            initRoles() {
                this.loading = true;
                this.getRequest("/system/basic/permiss/").then(resp => {
                    this.loading = false;
                    if (resp) {
                        this.roles = resp;
                    }
                })
            },
            change(id) {
                //折叠面板展开时id存在，关闭时不存在
                if (id) {
                    this.initMenus();
                    this.initSelectedMenus(id);
                }
            },
            initMenus() {
                this.getRequest("/system/basic/permiss/menus").then(resp => {
                    if (resp) {
                        this.allmenus = resp;
                    }
                })
            },
            initSelectedMenus(rid) {
                this.getRequest("/system/basic/permiss/mids/" + rid).then(resp => {
                    if (resp) {
                        this.selectedMenus = resp;
                    }
                })
            },
            doAddRole() {
                if (this.role.name && this.role.nameZh) {
                    this.postRequest("/system/basic/permiss/role", this.role).then(resp => {
                        if (resp) {
                            this.role.name = '';
                            this.role.nameZh = '';
                            this.initRoles();
                        }
                    })
                } else {
                    this.$message.error("添加字段不可以为空!");
                }
            }
            ,
            doUpdate(rid, index) {
                let tree = this.$refs.tree[index];
                let selectedKeys = tree.getCheckedKeys(true);
                let url = '/system/basic/permiss/?rid=' + rid;
                selectedKeys.forEach(key => {
                    url += '&mids=' + key;
                });
                this.putRequest(url).then(resp => {
                    if (resp) {
                        this.activeName = -1;
                        this.initRoles();
                    }
                })
            },
            cancelUpdate() {
                this.activeName = -1;
                this.$message("已取消修改");
            },
            deleteRole(role) {
                console.log("length>>" + this.selectedMenus.length)
                if (this.selectedMenus.length == 0) {
                    this.$confirm('此操作将永久删除【' + role.nameZh + '】角色, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.deleteRequest("/system/basic/permiss/role/" + role.id).then(resp => {
                            if (resp) {
                                this.initRoles();
                            }
                        })
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });
                    });
                } else {
                    this.$message("删除当前角色前，请先修改其可访问资源为空");
                }
            }
        }
    }
</script>

<style>
    .permissManaTool {
        display: flex;
        justify-content: flex-start;
    }

    .permissManaTool .el-input {
        width: 300px;
        margin-right: 5px;
    }

    .permissManaMain {
        margin-top: 10px;
        width: 800px;
    }
</style>